<template>
    <p-dialog
        :header="header"
        v-model:visible="createOrEditDialog"
        :style="{ width: '400px' }"
        :modal="true"
        class="p-inputtext-sm p-input-filled"
        @show="fillupForm"
    >
        <div class="p-field p-grid">
            <label for="firstname" class="p-col-4 p-mb-0">
                Cycle Type
            </label>
            <div class="p-col">
                <p-dropdown
                    v-model="cycle.cycleType"
                    id="cycle-type"
                    :options="cycleTypeOptions"
                    optionLabel="label"
                    optionValue="value"
                />
            </div>
        </div>

        <div class="p-field p-grid" v-if="isEditDailog">
            <label for="firstname" class="p-col-4 p-mb-0">
                Funded
            </label>
            <div class="p-col">
                <p-toggle-button
                    v-model="cycle.ohip"
                    id="egg-donor"
                    onIcon="pi pi-check"
                    offIcon="pi pi-times"
                />
            </div>
        </div>

        <div class="p-field p-grid" v-if="isEditDailog">
            <label for="firstname" class="p-col-4 p-mb-0">
                Cycle Cancelled
            </label>
            <div class="p-col">
                <p-toggle-button
                    v-model="cycle.cancelled"
                    id="egg-donor"
                    onIcon="pi pi-check"
                    offIcon="pi pi-times"
                />
            </div>
        </div>

        <div class="p-field p-grid" v-if="isEditDailog">
            <label for="firstname" class="p-col-4 p-mb-0">
                Sperm Source
            </label>
            <div class="p-col">
                <p-dropdown
                    v-model="cycle.spermSource"
                    id="cycle-type"
                    :filter="true"
                    :options="spermSourceOptions"
                    optionLabel="label"
                    optionValue="value"
                />
            </div>
        </div>

        <div class="p-field p-grid" v-if="isEditDailog">
            <label for="firstname" class="p-col-4 p-mb-0">
                Egg Source
            </label>
            <div class="p-col">
                <p-dropdown
                    v-model="cycle.eggSource"
                    id="cycle-type"
                    :filter="true"
                    :options="eggSourceOptions"
                    optionLabel="label"
                    optionValue="value"
                />
            </div>
        </div>

        <div class="p-field p-grid" v-if="isEditDailog">
            <label for="firstname" class="p-col-4 p-mb-0">
                Egg Donor
            </label>
            <div class="p-col">
                <p-toggle-button
                    v-model="cycle.eggDonor"
                    id="egg-donor"
                    onIcon="pi pi-check"
                    offIcon="pi pi-times"
                />
            </div>
        </div>

        <div class="p-field p-grid" v-if="isEditDailog">
            <label for="firstname" class="p-col-4 p-mb-0">
                Complete
            </label>
            <div class="p-col">
                <p-toggle-button
                    v-model="cycle.complete"
                    id="egg-donor"
                    onIcon="pi pi-check"
                    offIcon="pi pi-times"
                />
            </div>
        </div>

        <div class="p-grid p-mt-4">
            <p-button
                label="Edit"
                class="p-mr-2"
                @click="editCycle"
                v-if="isEditDailog"
                :loading="loading"
            />
            <p-button
                label="Save"
                class="p-mr-2"
                @click="createCycle"
                v-if="!isEditDailog"
                :loading="loading"
            />

            <p-button
                label="Cancel"
                class="p-button-danger p-button-outlined"
                @click="createOrEditDialog = false"
            />
        </div>
    </p-dialog>
</template>

<script>
import { onMounted } from "vue";
import { emitter } from "@/event-bus";
import { useCycleCreateOrEditDailogInit } from "@/composable/art-cycle";
import format from "date-fns/format";
import { DATE_FORMAT_STRING } from "@/config";

export default {
    setup() {
        const {
            store,
            selectedCycle,
            cycleTypeOptions,
            spermSourceOptions,
            eggSourceOptions,
            header,
            cycle,
            fillupForm,
            loading,
            createOrEditDialog,
            isEditDailog
        } = useCycleCreateOrEditDailogInit();

        const editCycle = () => {
            const born = {
                pNumber: store.state.artCycle.patient.id,
                createdBy: store.state.user.username,
                updatedBy: store.state.user.username,
                parentCycleType: store.state.artCycle.selectedCycle.cycleType,
                firstName: store.state.artCycle.patient.firstName,
                lastName: store.state.artCycle.patient.lastName,
                sex: "f",
                yearOfBirth: store.state.artCycle.patient.year,
                monthOfBirth: store.state.artCycle.patient.month,
                dateOfBirth: store.state.artCycle.patient.day
            };

            loading.value = true;
            store
                .dispatch("artCycle/updateCycle", {
                    ...born,
                    cycleNo: selectedCycle.value.cycleNo,
                    cycleId: selectedCycle.value.cycleId,
                    cycleType: cycle.cycleType,
                    cycleNoType: cycle.cycleNoType,
                    spermSource: cycle.spermSource,
                    eggSource: cycle.eggSource,
                    eggDonor: !!cycle.eggDonor,
                    cycleIncomplete: !cycle.complete,
                    cycleComplete: !!cycle.complete,
                    ohip: cycle.ohip,
                    cycleCancelled: !!cycle.cancelled,
                    others: cycle.others,
                    cycleDate: selectedCycle.value.cycleDate
                })
                .then(() => {
                    createOrEditDialog.value = false;
                    return store.dispatch(
                        "artCycle/getCyclesDetails",
                        store.state.artCycle.patient.id
                    );
                })
                .catch(() => {})
                .finally(() => {
                    loading.value = false;
                });
        };

        const createCycle = () => {
            loading.value = true;

            const born = {
                pNumber: store.state.artCycle.patient.id,
                createdBy: store.state.user.username,
                updatedBy: store.state.user.username,
                parentCycleType: cycle.cycleType,
                firstName: store.state.artCycle.patient.firstName,
                lastName: store.state.artCycle.patient.lastName,
                sex: "f",
                yearOfBirth: store.state.artCycle.patient.year,
                monthOfBirth: store.state.artCycle.patient.month,
                dateOfBirth: store.state.artCycle.patient.day
            };

            const _cycle = {
                ...born,
                patientId: store.state.artCycle.patient.id,
                cycleNo: store.state.artCycle.cyclesSummary.length + 1,
                cycleNumber: store.state.artCycle.cyclesSummary.length + 1,
                cycleType: cycle.cycleType,
                cycleNoType: 1,
                spermSource: "Parnter",
                eggSource: "Own",
                cycleDate: format(new Date(), DATE_FORMAT_STRING),
                cycleSucceeded: true,
                cycleExpired: false,
                eggDonor: true
            };

            store.dispatch("artCycle/createNewCycle", _cycle).then(() => {
                loading.value = false;
                createOrEditDialog.value = false;
            });
        };

        onMounted(() => {
            if (selectedCycle.value) {
                fillupForm(selectedCycle.value);
            }
            emitter.on("cycle-table:save-cycle", editCycle);
        });

        onMounted(() => {
            emitter.off("cycle-table:save-cycle", editCycle);
        });

        return {
            header,
            isEditDailog,
            createOrEditDialog,
            cycleTypeOptions,
            spermSourceOptions,
            eggSourceOptions,
            cycle,
            editCycle,
            createCycle,
            fillupForm,
            loading
        };
    }
};
</script>

<style scoped>
.p-dropdown {
    width: 100%;
}
</style>
